@import url(http://fonts.googleapis.com/css?family=Raleway:400,300,200,600,700);
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block; }

audio,
canvas,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active,
a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button,
input {
  line-height: normal; }

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
input[disabled] {
  cursor: default; }

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body,
figure {
  margin: 0; }

legend,
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
span,
li,
input,
textarea {
  font-family: "Raleway", "Helvetica", sans-serif; }

h1 {
  font-weight: 700;
  font-size: 4.375em; }

h2, h3, h4, h5, h6 {
  font-weight: 600; }

h2 {
  font-size: 3.75em; }

h3 {
  font-size: 3.125em; }

h4 {
  font-size: 2.5em; }

h5 {
  font-size: 2.188em; }

h6 {
  font-size: 1.875em; }

p, a, span, li, input, textarea {
  font-size: 1.25em; }

p, a, span, li {
  font-weight: 300; }

input, textarea {
  font-weight: 200; }

.navButton {
  height: 100px;
  position: absolute;
  width: 6.77966%;
  float: right;
  margin-right: 0;
  right: 6.77966%;
  background: none;
  border: none;
  overflow: hidden;
  color: white;
  font-size: 0;
  padding: 0;
  text-indent: -9999px;
  box-shadow: none;
  cursor: pointer;
  z-index: 20000; }
  .navButton:focus {
    outline: none; }
  .navButton span {
    display: block;
    position: absolute;
    top: 45px;
    left: 36px;
    right: 0;
    transition: background ease-in 0.3s;
    height: 3px;
    background: #fff; }
    .navButton span::before, .navButton span::after {
      position: absolute;
      display: block;
      left: 0;
      width: 100%;
      height: 3px;
      background: #fff;
      content: ""; }
    .navButton span::before {
      top: -27px; }
    .navButton span::after {
      bottom: -27px; }

.navX span {
  transition: background ease-out 0 0.5s; }
  .navX span::after, .navX span::before {
    transition-duration: 0.5s, 0.5s;
    transition-delay: 1.4s, 0; }
  .navX span::before {
    transition-property: top, transform; }
  .navX span::after {
    transition-property: bottom, transform; }

.navX.active span {
  background: none; }
  .navX.active span::before {
    top: 0;
    transform: rotate(45deg); }
  .navX.active span::after {
    bottom: 0;
    transform: rotate(-45deg); }
  .navX.active span::before,
  .navX.active span::after {
    transition-delay: 0, 0.5s; }

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10000;
  color: #fff;
  display: none; }
  .overlay .navContent {
    bottom: 20%;
    position: absolute;
    width: 100%; }
    .overlay .navContent ul {
      font-size: 1.875em;
      list-style: none;
      text-align: right;
      margin-right: 6.77966%; }
    .overlay .navContent li {
      display: none;
      opacity: 0.8; }
      .overlay .navContent li span {
        font-weight: 600; }

.hero {
  height: 100vh;
  background: url("https://download.unsplash.com/13/unsplash_523b2af0710a7_1.JPG") no-repeat fixed;
  background-size: cover;
  padding: 1px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto; }
  .hero:after {
    content: " ";
    display: block;
    clear: both; }
  .hero h1 {
    color: white;
    position: relative;
    text-align: center;
    top: 30%;
    font-weight: 200;
    width: 50%;
    margin: 0 auto; }
    .hero h1 span {
      text-transform: uppercase;
