  .hero .navBar {
    position: absolute;
    bottom: 10%;
    left: 0;
    right: 0; }
    .hero .navBar .logo {
      display: inline-block;
      width: 23.72881%;
      float: left;
      margin-right: 1.69492%;
      margin-left: 6.77966%; }
    .hero .navBar .navButton {
      width: 6.77966%;
      float: right;
      margin-right: 0; }
